/* ------------------------------------------------------------------------------------------------------------------------------
ROOT
--------------------------------------------------------------------------------------------------------------------------------*/
:root {
  --container-width: 1440px;
  --container-padding-desktop: 165px;
  --container-padding-large: 50px;
  --container-padding-small: 10px;
  --font-main: "Inter", sans-serif;
  --font-title: "Inter", sans-serif;
  --white: white;
  --black: black;
  --red: red;
  --text: #C2F9FF;
  --transition: all 0.8s ease-in-out;
}

/* ------------------------------------------------------------------------------------------------------------------------------
KEYFRAMES
--------------------------------------------------------------------------------------------------------------------------------*/
@keyframes show-header {
  0% {
    inset-block-start: -100%;
  }
  100% {
    inset-block-start: 0;
  }
}
@keyframes fade-up-big {
  0% {
    opacity: 0;
    transform: translateY(1rem);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}
@keyframes fade-up {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
@keyframes rotation {
  0% {
    transform: rotate(0);
  }
  100% {
    transform: rotate(359deg);
  }
}
/* ------------------------------------------------------------------------------------------------------------------------------
MEDIAQUERIES 
--------------------------------------------------------------------------------------------------------------------------------*/
* {
  margin: 0;
  border: 0;
  padding: 0;
  -webkit-tap-highlight-color: transparent;
}
*, *::before, *::after {
  box-sizing: border-box;
}
*:focus, *:active {
  outline: none;
}

nav,
footer,
header,
aside {
  display: block;
}

html {
  opacity: 0;
  scroll-behavior: smooth;
  transition: var(--transition);
}
html.loaded {
  opacity: 1;
}

html,
body {
  inline-size: 100%;
  block-size: 100%;
  min-inline-size: 320px;
}

body {
  overscroll-behavior: none;
  text-rendering: optimizelegibility;
  text-size-adjust: 100%;
  font-family: var(--font-main);
  font-size: 16px;
  font-weight: 400;
  -moz-osx-font-smoothing: grayscale;
  -webkit-font-smoothing: antialiased;
}

input,
button,
textarea {
  font-family: inherit;
}

input::-ms-clear {
  display: none;
}

input[type=number] {
  appearance: textfield;
}
input[type=number]::-webkit-outer-spin-button, input[type=number]::-webkit-inner-spin-button {
  appearance: none;
}

button {
  cursor: pointer;
  user-select: none;
  background-color: transparent;
}
button::-moz-focus-inner {
  border: 0;
  padding: 0;
}

a {
  user-select: none;
  text-decoration: none;
}
a:hover {
  text-decoration: none;
}
a:focus, a:active {
  outline: none;
}
a:visited {
  text-decoration: none;
}

ul,
li {
  list-style: none;
}

img {
  vertical-align: top;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: inherit;
  font-weight: 400;
}

sup {
  color: inherit;
}

/* ------------------------------------------------------------------------------------------------------------------------------
TYPOGRAPHY
--------------------------------------------------------------------------------------------------------------------------------*/
h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: var(--font-title);
}

/* ------------------------------------------------------------------------------------------------------------------------------
BASE
--------------------------------------------------------------------------------------------------------------------------------*/
* {
  margin: 0;
  border: 0;
  padding: 0;
  -webkit-tap-highlight-color: transparent;
}
*, *::before, *::after {
  box-sizing: border-box;
}
*:focus, *:active {
  outline: none;
}

nav,
footer,
header,
aside {
  display: block;
}

html {
  scroll-behavior: smooth;
}

html,
body {
  inline-size: 100%;
  block-size: 100%;
}

body {
  overscroll-behavior: none;
  text-rendering: optimizelegibility;
  text-size-adjust: 100%;
  font-family: var(--font-main);
  font-size: 16px;
  font-weight: 400;
  -moz-osx-font-smoothing: grayscale;
  -webkit-font-smoothing: antialiased;
  text-wrap: balance;
}

.container,
[class*=__container] {
  margin-inline: auto;
  min-inline-size: 320px;
  max-inline-size: var(--container-width);
  padding-inline: var(--container-padding-small);
}
@media (min-width: 640px) {
  .container,
  [class*=__container] {
    padding-inline: var(--container-padding-large);
  }
}
@media (min-width: 1280px) {
  .container,
  [class*=__container] {
    padding-inline: var(--container-padding-desktop);
  }
}

[class*=__container--wide50] {
  margin-inline: auto;
  min-inline-size: 320px;
  max-inline-size: calc(var(--container-width) + 50px);
  padding-inline: var(--container-padding-small);
}
@media (min-width: 640px) {
  [class*=__container--wide50] {
    padding-inline: var(--container-padding-large);
  }
}
@media (min-width: 1280px) {
  [class*=__container--wide50] {
    padding-inline: var(--container-padding-desktop);
  }
}

.icon {
  max-width: 50px;
  max-height: 50px;
}

.img-responsive {
  display: block;
  width: 100%;
  height: auto;
  object-fit: cover;
  overflow: hidden;
}

.image-bg {
  position: relative;
}
.image-bg img {
  display: block;
  position: absolute;
  inset-block-start: 0;
  inset-inline-start: 0;
  object-fit: cover;
  block-size: 100%;
  inline-size: 100%;
}

.wrapper {
  display: flex;
  min-block-size: 100%;
  /** Поменять на clip - если используется в проекте position: sticky */
  overflow: hidden;
  flex-direction: column;
}

.main {
  background: #2f6bc3;
  position: relative;
  flex: 1 1 auto;
  display: flex;
}
@media (min-width: 640px) {
  .main {
    background: #3f82d3;
  }
}

/* ------------------------------------------------------------------------------------------------------------------------------
COMPONENTS
--------------------------------------------------------------------------------------------------------------------------------*/
.rounded-button-sm {
  -webkit-appearance: none;
  appearance: none;
  box-sizing: border-box;
  border-radius: 27px;
  /* Safari backdrop fix */
  backdrop-filter: blur(122.5px);
  -webkit-backdrop-filter: blur(122.5px);
  background: rgba(255, 255, 255, 0.11);
  padding: 6px 22px; /* вместо height */
  min-height: 33px;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  /* старые iOS иногда плохо с gap */
  gap: 6px;
  font-family: var(--font-family);
  font-weight: 600;
  font-size: 16px;
  line-height: 1;
  color: #fff;
  border: 1px solid #92a8ff;
  cursor: pointer;
  transition: background 0.25s ease, border 0.25s ease, transform 0.08s ease;
  /* сглаживание текста на Apple */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  /* фикс странных обрезаний blur */
  background-clip: padding-box;
}
.rounded-button-sm svg {
  width: 20px;
  height: 20px;
  flex-shrink: 0;
  display: block;
}
.rounded-button-sm:hover {
  background: rgba(255, 255, 255, 0.28);
}
.rounded-button-sm:active {
  transform: scale(0.97);
}
.rounded-button-sm:focus-visible {
  outline: 2px solid #92efff;
  outline-offset: 2px;
}

.rounded-button-lg {
  -webkit-appearance: none;
  appearance: none;
  box-sizing: border-box;
  border-radius: 27px;
  /* blur фикс для Safari */
  backdrop-filter: blur(12px);
  -webkit-backdrop-filter: blur(12px);
  background: rgba(255, 255, 255, 0.16);
  background-clip: padding-box;
  /* вместо height — стабильнее на iOS */
  padding: 14px 22px;
  min-height: 57px;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  gap: 10px;
  font-family: var(--font-family);
  font-weight: 600;
  font-size: 24px;
  line-height: 1;
  color: #fff;
  text-align: center;
  /* Apple текст фикс */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-shadow: 0 1px 20px rgba(0, 0, 0, 0.1);
  border: 1px solid #92a8ff;
  cursor: pointer;
  /* не юзай transition: all — Safari лагает */
  transition: background 0.25s ease, border 0.25s ease, transform 0.08s ease, box-shadow 0.25s ease;
  /* фикс blur артефактов */
  will-change: backdrop-filter, transform;
  /* нормальный фокус для Apple */
}
.rounded-button-lg svg {
  width: 22px;
  height: 22px;
  flex-shrink: 0;
  display: block;
}
.rounded-button-lg:hover {
  background: rgba(255, 255, 255, 0.3);
}
.rounded-button-lg:active {
  transform: scale(0.97);
}
.rounded-button-lg:focus-visible {
  outline: 2px solid #92efff;
  outline-offset: 3px;
}

.rounded-sky-button-lg {
  -webkit-appearance: none;
  appearance: none;
  box-sizing: border-box;
  flex-shrink: 0;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  border-radius: 20px;
  padding: 4px 17px;
  min-height: 20px;
  font-family: var(--font-family);
  font-weight: 900;
  font-size: 8px;
  line-height: 1;
  text-align: center;
  color: #fff;
  /* текст красивее на Apple */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-shadow: 0 1px 20px rgba(0, 0, 0, 0.1);
  border: 1px solid #92a8ff;
  /* blur фикс */
  backdrop-filter: blur(11px);
  -webkit-backdrop-filter: blur(11px);
  /* градиент стабильный */
  background: linear-gradient(140deg, rgba(0, 255, 252, 0.87) 0%, rgba(0, 188, 255, 0.87) 100%);
  background-clip: padding-box;
  cursor: pointer;
  /* Safari лаги убираем */
  transition: opacity 0.25s ease, transform 0.08s ease, box-shadow 0.25s ease;
  will-change: transform, opacity, backdrop-filter;
  /* tap фикс iOS */
  -webkit-tap-highlight-color: transparent;
  touch-action: manipulation;
  /* ===== SM ===== */
  /* ===== XL ===== */
}
.rounded-sky-button-lg:hover {
  opacity: 0.8;
}
.rounded-sky-button-lg:active {
  transform: scale(0.97);
}
.rounded-sky-button-lg:focus-visible {
  outline: 2px solid #92efff;
  outline-offset: 2px;
}
@media (min-width: 640px) {
  .rounded-sky-button-lg {
    border-radius: 24px;
    padding: 8px 20px;
    min-height: 40px;
    font-size: 16px;
  }
}
@media (min-width: 1280px) {
  .rounded-sky-button-lg {
    border-radius: 27px;
    padding: 14px 22px;
    min-height: 57px;
    font-weight: 900;
    font-size: 24px;
  }
}

.cooperation-input {
  -webkit-appearance: none;
  appearance: none;
  box-sizing: border-box;
  flex-grow: 1;
  width: 100%;
  display: block;
  /* вместо height — стабильнее */
  padding: 6px 8px;
  min-height: 19px;
  border-radius: 4px;
  font-family: var(--font-family);
  font-weight: 400;
  font-size: 8px;
  line-height: 1.2;
  color: #fff;
  /* текст красивый на Apple */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  /* blur фикс */
  backdrop-filter: blur(10px);
  -webkit-backdrop-filter: blur(10px);
  background: rgba(255, 255, 255, 0.11);
  background-clip: padding-box;
  border: 1px solid #92efff;
  transition: border 0.2s ease, box-shadow 0.2s ease, background 0.2s ease;
  /* убирает zoom iOS при фокусе */
  transform: translateZ(0);
  /* убирает системный стиль iOS */
  -webkit-tap-highlight-color: transparent;
  /* placeholder */
  /* focus */
  /* ===== SM ===== */
  /* ===== XL ===== */
}
.cooperation-input::placeholder {
  color: rgba(255, 255, 255, 0.75);
  opacity: 1; /* safari фикс */
}
.cooperation-input:focus {
  outline: none;
  border: 1px solid #92efff;
  box-shadow: 0 0 0 2px rgba(146, 239, 255, 0.35);
}
@media (min-width: 640px) {
  .cooperation-input {
    padding: 10px 12px;
    min-height: 40px;
    border-radius: 10px;
    font-size: 16px; /* важно: >=16px чтобы iOS не зумил */
  }
}
@media (min-width: 1280px) {
  .cooperation-input {
    padding: 14px 17px;
    min-height: 57px;
    border-radius: 12px;
    font-size: 24px;
  }
}

/* ------------------------------------------------------------------------------------------------------------------------------
HEADER
--------------------------------------------------------------------------------------------------------------------------------*/
.header {
  min-height: 200px;
  padding-bottom: 20px;
  /* фикс рендеринга на Safari */
  box-sizing: border-box;
  position: relative;
  z-index: 1;
}
@media (min-width: 640px) {
  .header {
    padding-bottom: 35px;
  }
}
.header.mobile {
  background: url("../images/mobile-top-bg.webp") no-repeat 0 0;
  background-size: 100% 100%;
  /* фикс дерганий фона на iOS */
  background-attachment: scroll;
}
@media (min-width: 640px) {
  .header.desktop {
    background: url("../images/desktop-top-bg.webp") no-repeat 0 0;
    background-size: 100% 100%;
    background-attachment: scroll;
  }
}
.header__row {
  height: 55px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  /* Safari flex fix */
  min-width: 0;
}
@media (min-width: 640px) {
  .header__row {
    height: 75px;
  }
}
.header__logo {
  display: block;
  aspect-ratio: 158/37;
  width: 158px;
  /* чтобы не схлопывался в flex */
  flex-shrink: 0;
}
.header__cooperation {
  display: flex;
  flex-direction: column;
  gap: 10px;
  font-family: var(--font-family);
  font-weight: 900;
  font-size: 11px;
  text-align: center;
  color: #c2f9ff;
  /* стабильность текста */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
@media (min-width: 640px) {
  .header__cooperation {
    gap: 18px;
    font-size: 18px;
  }
}
@media (min-width: 1280px) {
  .header__cooperation {
    gap: 25px;
    font-family: var(--font-family);
    font-weight: 900;
    font-size: 23px;
    text-align: center;
    color: #c2f9ff;
  }
}
.header h1 {
  font-family: var(--font-family);
  font-weight: 700;
  font-size: 28px;
  color: #fff;
  margin: 0; /* safari стандартный margin убираем */
  /* красивый текст на apple */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
@media (min-width: 640px) {
  .header h1 {
    font-size: 48px;
  }
}
@media (min-width: 1280px) {
  .header h1 {
    font-family: var(--font-family);
    font-weight: 700;
    font-size: 96px;
    color: #fff;
  }
}
.header .rounded-button-lg {
  margin-inline: auto;
  margin-top: 10px;
  /* фикс Safari margin auto */
  display: flex;
  justify-content: center;
}

/* ------------------------------------------------------------------------------------------------------------------------------
MAIN CONTAINER / HEADER
--------------------------------------------------------------------------------------------------------------------------------*/
.main__container {
  flex-grow: 1;
  overflow: hidden;
  background-position: 0 0;
  background-repeat: no-repeat;
  /* стабильность layout */
  box-sizing: border-box;
  position: relative;
  z-index: 1;
  min-width: 0;
  padding-block-start: 40px;
  border-top-left-radius: 34px;
  border-top-right-radius: 34px;
}
@media (min-width: 640px) {
  .main__container {
    padding-block-start: 50px;
    border-top-left-radius: 54px;
    border-top-right-radius: 54px;
  }
}
@media (min-width: 1280px) {
  .main__container {
    border-top-left-radius: 74px;
    border-top-right-radius: 74px;
  }
}
.main__container.mobile {
  background: url("../images/mobile-bg.webp") no-repeat 0 0;
  background-size: 100% 100%;
  padding-block-end: 50px;
  /* iOS bg repaint fix */
  background-attachment: scroll;
}
@media (min-width: 640px) {
  .main__container.desktop {
    background: url("../images/desktop-bg.webp") no-repeat 0 0;
    background-size: 100% 100%;
    padding-block-end: 165px;
    background-attachment: scroll;
  }
}

/* ------------------------------------------------------------------------------------------------------------------------------
MAIN SECTION
--------------------------------------------------------------------------------------------------------------------------------*/
.main__section {
  display: flex;
  flex-direction: column;
  gap: 2px;
  /* safari flex fix */
  min-width: 0;
}
@media (min-width: 640px) {
  .main__section {
    gap: 20px;
  }
}
@media (min-width: 1280px) {
  .main__section {
    gap: 35px;
  }
}

/* ------------------------------------------------------------------------------------------------------------------------------
CARD STYLES
--------------------------------------------------------------------------------------------------------------------------------*/
.main__card {
  display: flex;
  flex-direction: column;
  align-items: center;
  color: var(--text);
  /* чтобы blur/transform не лагал */
  position: relative;
  z-index: 1;
}
.main__cardimage {
  width: 160px;
  height: 160px;
  /* чтобы img не схлопывался */
  flex-shrink: 0;
}
@media (min-width: 640px) {
  .main__cardimage {
    width: 220px;
    height: 220px;
  }
}
@media (min-width: 1280px) {
  .main__cardimage {
    width: 300px;
    height: 300px;
  }
}
.main__cardimage img {
  display: block;
  width: 100%;
  height: auto;
  object-fit: cover;
  overflow: hidden;
  /* Safari image render fix */
  -webkit-user-drag: none;
  user-select: none;
}
.main__cardform {
  font-family: var(--font-family);
  font-weight: 600;
  text-align: center;
  color: #c2f9ff;
  width: 100%;
  font-size: 14px;
  margin-top: 10px;
  /* текст стабильнее */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
@media (min-width: 640px) {
  .main__cardform {
    font-size: 22px;
    margin-top: 15px;
  }
}
@media (min-width: 1280px) {
  .main__cardform {
    font-size: 23px;
    margin-top: 20px;
  }
}
.main__cardform h2 {
  margin-bottom: 5px;
}
.main__cardform form {
  margin-top: 25px;
}
@media (min-width: 640px) {
  .main__cardform form {
    margin-top: 40px;
  }
}
@media (min-width: 1280px) {
  .main__cardform form {
    margin-top: 60px;
  }
}

