  will-change: transform;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  /* верхняя маска */
  /* нижняя маска */
}
@media (min-width: 640px) {
  .main__block {
    font-size: 24px;
  }
}
@media (min-width: 1280px) {
  .main__block {
