  }
}
@media (min-width: 1280px) {
  .main__block h2.mob-22 {
    font-size: 48px;
  }
}
.main__block h2.mob-24 {
  font-size: 24px;
}
@media (min-width: 640px) {
  .main__block h2.mob-24 {
    font-size: 36px;
  }
}
@media (min-width: 1280px) {
  .main__block h2.mob-24 {
    font-size: 48px;
  }
}
.main__block h2.mob-25 {
  font-size: 25px;
}
@media (min-width: 640px) {
  .main__block h2.mob-25 {
    font-size: 38px;
  }
}
@media (min-width: 1280px) {
  .main__block h2.mob-25 {
    font-size: 48px;
  }
}
.main__block::before {
  content: "";
  position: absolute;
  top: 25%;
  left: 50%;
  width: 80%;
  height: 100px;
  transform: translateX(-50%) translateZ(0);
  border-radius: 50%;
  background: linear-gradient(211deg, #00d0ff 0%, #0015ff 100%);
  filter: blur(40px);
  z-index: 0;
  pointer-events: none;
}
@media (min-width: 768px) {
  .main__block::before {
    width: 70%;
    height: 110px;
    filter: blur(60px);
  }
}
@media (min-width: 1024px) {
  .main__block::before {
    height: 120px;
    filter: blur(80px);
  }
}
.main__block::after {
  content: "";
  position: absolute;
  top: 40%;
  left: 50%;
  width: 180%;
  height: 30px;
  transform: translateX(-50%) rotate(-5deg) translateZ(0);
  border-radius: 50%;
  background: conic-gradient(from 90deg, #00d0ff 0deg, #0015ff 360deg);
  filter: blur(40px);
  opacity: 0.9;
  z-index: 0;
  pointer-events: none;
}
@media (min-width: 768px) {
  .main__block::after {
    height: 115px;
    filter: blur(70px);
  }
}
@media (min-width: 1024px) {
  .main__block::after {
    height: 130px;
    filter: blur(80px);
  }
}
.main__block .main__card {
  position: relative;
  z-index: 1;
}

/* ------------------------------------------------------------------------------------------------------------------------------
CARD TILE
--------------------------------------------------------------------------------------------------------------------------------*/
.main__cardtile {
  padding-bottom: 20px;
  backdrop-filter: blur(11.5px);
  -webkit-backdrop-filter: blur(11.5px);
  background: rgba(255, 255, 255, 0.11);
  background-clip: padding-box;
  border: #92a8ff 1px solid;
  width: 100%;
  border-radius: 16px;
  padding-inline: 2px;
  padding-block-start: 7px;
  padding-block-end: 11px;
  /* Safari blur fix */
  transform: translateZ(0);
  will-change: backdrop-filter;
}
@media (min-width: 1280px) {
  .main__cardtile {
    border-radius: 27px;
    padding-block: 20px;
    padding-inline: 35px;
  }
}
.main__cardtile-row {
  display: flex;
  align-items: center;
  padding-inline: 10px;
  gap: 10px;
  border-radius: 9px;
  padding-block: 7px;
  /* flex баги safari */
  min-width: 0;
}
@media (min-width: 640px) {
  .main__cardtile-row {
    gap: 20px;
    border-radius: 15px;
    padding-block: 15px;
  }
}
@media (min-width: 1280px) {
  .main__cardtile-row {
    gap: 40px;
    border-radius: 27px;
    padding-block: 20px;
  }
}

